import { createApp } from "vue"
import App from "./App.vue"
import AuthTemplate from "./components/auth/AuthTemplate.vue"
import AuthFormComponent from "./components/auth/AuthFormComponent.vue"
import HomeTemplate from "./components/home/HomeTemplate.vue"

let app = createApp(App)
app.component("auth-template", AuthTemplate)
app.component("auth-form-component", AuthFormComponent)
app.component("home-template", HomeTemplate)
app.mount("#app")
