<template>
  <input type="radio" name="form-mode" id="register" value="/register" v-model="submitTo" checked>
  <label for="register">新規登録</label>
  <input type="radio" name="form-mode" id="login" value="/login" v-model="submitTo">
  <label for="login">ログイン</label>
  <h2>{{ formTitle }}</h2>
  <auth-form-component :submitTo="submitTo"/>
</template>

<script>
import { defineComponent } from "@vue/composition-api";
import AuthFormComponent from "./AuthFormComponent.vue";
import { ref, watchEffect } from "vue"

export default defineComponent({
  components: { AuthFormComponent },
  setup() {
    const submitTo = ref("/register")
    const formTitle = ref("新規登録")

    watchEffect(() => {
      if(submitTo.value == "/register") {
        formTitle.value = "新規登録"
      }else {
        formTitle.value = "ログイン"
      }
    })

    return {
      submitTo,
      formTitle,
    }
  },
})
</script>
