<template>
  <auth-template v-on:login="login" v-show="!isLoggingIn"/>
  <home-template v-show="isLoggingIn"></home-template>
</template>

<script>
import { defineComponent } from "@vue/composition-api";

export default defineComponent({
  setup() {
    let isLoggingIn = false

    const login = () => {
      isLoggingIn = true;
    }

    return {
      isLoggingIn,
      login,
    }
  },
})
</script>
